import React, { useState } from 'react';
import Items from './components/Items.jsx';
import Input from './components/Input.jsx';

import './App.css';
import DATA from './mocks/API_DATA.json';

const App = () => {

  const [data, setData] = useState(DATA);
  const [val, setVal] = useState('');

  const onValueSubmit = (e) => {
  setVal(e); 
  const items = DATA.items.filter(item => item.namePL.includes(e.toLocaleUpperCase()));
  setData({items});
  }

  return (
    <div className="App">
      <header>
        <h1>Filmy <span>2021</span> na ktre mona gosowa</h1>
        <Input 
        value = {val}
        onFormSubmit = {onValueSubmit} 
        />
      </header>
      <main>
        <Items data = {data} />
      </main>
      
    </div>
  );
}

export default App;
